// =============================================================================
// x402 Demo Endpoint
// =============================================================================
// POST /api/v1/x402/demo/resource
// 
// Demo endpoint that demonstrates x402 payment flow:
// - First call (no payment): Returns 402 with payment requirements
// - Second call (with payment): Verifies payment and returns 200

import { NextRequest, NextResponse } from 'next/server';
import { requireAuth } from '@/lib/auth';
import { rateLimitByApiKey, addRateLimitHeaders, createRateLimitResponse } from '@/lib/rate-limit-unified';
import { createLogger } from '@/lib/logger';

const demoLogger = createLogger({ component: 'X402DemoResource' });
import { extractRequestContext, wrapResponse, createErrorResponse } from '@/lib/request-logging';
import { buildPaymentRequirements, verifyPaymentFromRequest, DEMO_PAYMENT_AMOUNT_ATOMIC } from '@/lib/x402-demo';
import { hasX402DemoAllowance, updateApiKeyX402DemoAllowance, findApiKeyByToken } from '@/db/api-keys';
import { extractApiKey } from '@/lib/auth';
import { logX402Call } from '@/integrations/x402/x402-call-log';

/**
 * POST /api/v1/x402/demo/resource
 * 
 * x402 demo endpoint
 * 
 * First call: Returns 402 Payment Required with x402 payment requirements
 * Second call (with x-payment header): Verifies payment and returns 200
 */
export async function POST(request: NextRequest) {
  const ctx = await extractRequestContext(request, '/api/v1/x402/demo/resource');
  
  try {
    // Check rate limit
    const rateLimitResult = await rateLimitByApiKey(request);
    if (rateLimitResult && !rateLimitResult.allowed) {
      demoLogger.warn({ apiKeyId: ctx.apiKeyId, limit: rateLimitResult.limit }, 'Rate limit exceeded');
      return wrapResponse(createRateLimitResponse(rateLimitResult), ctx);
    }
    
    // Require authentication
    const authResult = await requireAuth(request, 'user');
    if ('response' in authResult) {
      return wrapResponse(authResult.response, ctx);
    }
    
    const { apiKey } = authResult;
    ctx.apiKeyId = apiKey.id;
    
    // Build resource URL
    const baseUrl = process.env.NEXT_PUBLIC_APP_URL || 
                   (request.headers.get('x-forwarded-proto') === 'https' ? 'https' : 'http') + 
                   '://' + (request.headers.get('host') || 'localhost:3001');
    const resourceUrl = `${baseUrl}/api/v1/x402/demo/resource`;
    
    // Build payment requirements
    const paymentRequirements = buildPaymentRequirements({
      resource: resourceUrl,
      network: 'base',
      asset: process.env.USDC_ADDRESS || '0x833589fCD6eDb6E08f4c7C32D4f71b54bdA02913',
      recipientAddress: process.env.X402_RECIPIENT_ADDRESS,
    });
    
    // Check if payment header is present
    const paymentHeader = request.headers.get('x-payment');
    
    if (!paymentHeader) {
      // First call: Return 402 Payment Required
      const response = NextResponse.json(
        {
          x402Version: 2,
          error: 'Payment required',
          accepts: [
            {
              scheme: paymentRequirements.scheme,
              network: paymentRequirements.network,
              maxAmountRequired: paymentRequirements.value,
              asset: paymentRequirements.asset,
              payTo: paymentRequirements.to,
              resource: paymentRequirements.resource,
              description: paymentRequirements.description,
              mimeType: paymentRequirements.mimeType,
              maxTimeoutSeconds: paymentRequirements.maxTimeoutSeconds,
              validAfter: paymentRequirements.validAfter,
              validBefore: paymentRequirements.validBefore,
            },
          ],
        },
        { status: 402 }
      );
      
      // Log 402 response
      await logX402Call({
        endpointId: 'x402_demo_resource',
        resource: resourceUrl,
        facilitator: 'CDP',
        chainId: 8453, // Base
        asset: paymentRequirements.asset,
        amount: paymentRequirements.value,
        status: 'payment_required',
        errorCode: 'PAYMENT_REQUIRED',
        latencyMs: Date.now() - ctx.startTime,
        customerId: ctx.requestId,
      });
      
      if (rateLimitResult) {
        addRateLimitHeaders(response, rateLimitResult);
      }
      
      return wrapResponse(response, ctx);
    }
    
    // Second call: Verify payment
    // Check demo allowance first
    const allowance = hasX402DemoAllowance(apiKey);
    if (!allowance.hasAllowance) {
      const response = NextResponse.json(
        {
          code: 'X402_DEMO_LIMIT_REACHED',
          message: 'Your free x402 demo allowance is exhausted. Connect your own wallet or upgrade to continue.',
          limits: {
            callsUsed: apiKey.x402DemoCallsUsed || 0,
            callsLimit: apiKey.x402DemoCallsLimit || 200,
            amountUsed: apiKey.x402DemoAmountUsed || '0',
            amountLimit: apiKey.x402DemoAmountLimit || '1000000',
            callsRemaining: allowance.callsRemaining,
            amountRemaining: allowance.amountRemaining,
          },
        },
        { status: 403 }
      );
      
      await logX402Call({
        endpointId: 'x402_demo_resource',
        resource: resourceUrl,
        facilitator: 'CDP',
        chainId: 8453,
        asset: paymentRequirements.asset,
        amount: paymentRequirements.value,
        status: 'failed',
        errorCode: 'X402_DEMO_LIMIT_REACHED',
        latencyMs: Date.now() - ctx.startTime,
        customerId: ctx.requestId,
      });
      
      return wrapResponse(response, ctx);
    }
    
    // Verify payment
    const verifyResult = await verifyPaymentFromRequest(request, {
      requestId: ctx.requestId,
      apiKeyId: apiKey.id,
      endpoint: '/api/v1/x402/demo/resource',
      paymentRequirements,
    });
    
    if (!verifyResult.ok) {
      const response = NextResponse.json(
        {
          code: verifyResult.errorCode,
          message: verifyResult.details,
        },
        { status: verifyResult.status }
      );
      
      await logX402Call({
        endpointId: 'x402_demo_resource',
        resource: resourceUrl,
        facilitator: 'CDP',
        chainId: 8453,
        asset: paymentRequirements.asset,
        amount: paymentRequirements.value,
        status: 'failed',
        errorCode: verifyResult.errorCode,
        latencyMs: Date.now() - ctx.startTime,
        customerId: ctx.requestId,
      });
      
      return wrapResponse(response, ctx);
    }
    
    // Payment verified - update allowance
    const newCallsUsed = (apiKey.x402DemoCallsUsed || 0) + 1;
    const currentAmountUsed = BigInt(apiKey.x402DemoAmountUsed || '0');
    const paymentAmount = BigInt(verifyResult.amount);
    const newAmountUsed = (currentAmountUsed + paymentAmount).toString();
    
    // Update allowance atomically
    await updateApiKeyX402DemoAllowance(apiKey.id, newCallsUsed, newAmountUsed);
    
    // Log successful payment
    await logX402Call({
      endpointId: 'x402_demo_resource',
      resource: resourceUrl,
      facilitator: 'CDP',
      chainId: 8453,
      asset: verifyResult.asset,
      amount: verifyResult.amount,
      status: 'success',
      x402TxHash: verifyResult.transactionHash,
      latencyMs: Date.now() - ctx.startTime,
      customerId: ctx.requestId,
    });
    
    // Return success response
    const response = NextResponse.json({
      success: true,
      demo: true,
      message: 'Paid x402 demo resource via NexFlow',
      paymentReference: verifyResult.reference,
      transactionHash: verifyResult.transactionHash,
      amount: verifyResult.amount,
      asset: verifyResult.asset,
      network: verifyResult.network,
      allowance: {
        callsUsed: newCallsUsed,
        callsLimit: apiKey.x402DemoCallsLimit || 200,
        amountUsed: newAmountUsed,
        amountLimit: apiKey.x402DemoAmountLimit || '1000000',
      },
    });
    
    if (rateLimitResult) {
      addRateLimitHeaders(response, rateLimitResult);
    }
    
    return wrapResponse(response, ctx);
  } catch (error) {
    return createErrorResponse(
      ctx,
      error instanceof Error ? error : new Error('Internal server error'),
      500,
      'INTERNAL_ERROR'
    );
  }
}

