// =============================================================================
// x402 Demo Helper Library
// =============================================================================
// Utilities for building payment requirements and verifying payments for the demo endpoint

import { NextRequest } from 'next/server';
import { getCDPFacilitator, type CDPVerifyRequest, type X402PaymentRequirements } from '@/integrations/x402/cdp-facilitator';
import { parseX402Header } from '@/integrations/x402/payment-header-parser';
import { toAtomicUnits, normalizeNetwork } from '@/integrations/x402/amount-utils';
import { createLogger, type LogContext } from '@/lib/logger';

const logger = createLogger({ component: 'X402Demo' });

/**
 * Demo payment amount configuration
 * Small amount for demo tier (0.001-0.01 USD equivalent)
 */
export const DEMO_PAYMENT_AMOUNT = '0.005'; // ~$0.005 USD
export const DEMO_PAYMENT_AMOUNT_ATOMIC = toAtomicUnits(DEMO_PAYMENT_AMOUNT, 6); // USDC has 6 decimals

/**
 * Build payment requirements for 402 response
 */
export function buildPaymentRequirements(options: {
  resource: string;
  network?: string;
  asset?: string;
  recipientAddress?: string;
}): X402PaymentRequirements {
  const network = normalizeNetwork(options.network || 'base');
  const asset = options.asset || process.env.USDC_ADDRESS || '0x833589fCD6eDb6E08f4c7C32D4f71b54bdA02913'; // Base mainnet USDC
  const recipientAddress = options.recipientAddress || process.env.X402_RECIPIENT_ADDRESS || '';
  
  if (!recipientAddress) {
    throw new Error('Recipient address is required for x402 demo payments');
  }
  
  const now = Math.floor(Date.now() / 1000);
  const validAfter = now.toString();
  const validBefore = (now + 300).toString(); // 5 minute window
  
  return {
    scheme: 'exact',
    network,
    to: recipientAddress,
    value: DEMO_PAYMENT_AMOUNT_ATOMIC,
    resource: options.resource,
    asset,
    validAfter,
    validBefore,
    description: 'NexFlow x402 Demo Resource',
    mimeType: 'application/json',
    maxTimeoutSeconds: 300,
  };
}

/**
 * Verify payment from request
 * Parses x402 payment header and verifies with CDP
 */
export async function verifyPaymentFromRequest(
  request: NextRequest,
  options: {
    requestId?: string;
    apiKeyId?: string;
    endpoint?: string;
    paymentRequirements: X402PaymentRequirements;
  }
): Promise<{
  ok: true;
  reference: string;
  amount: string;
  asset: string;
  network: string;
  transactionHash?: string;
} | {
  ok: false;
  errorCode: string;
  status: number;
  details: string;
}> {
  const logContext: LogContext = {
    requestId: options.requestId,
    apiKeyId: options.apiKeyId,
    endpoint: options.endpoint,
  };
  
  try {
    // Extract payment header
    const paymentHeader = request.headers.get('x-payment');
    if (!paymentHeader) {
      logger.warn(logContext, 'Missing x-payment header');
      return {
        ok: false,
        errorCode: 'PAYMENT_REQUIRED',
        status: 402,
        details: 'x-payment header is required',
      };
    }
    
    // Parse payment header
    const parseResult = parseX402Header(paymentHeader);
    if (!parseResult.valid || !parseResult.parsed) {
      logger.warn({ ...logContext, error: parseResult.error }, 'Invalid payment header format');
      return {
        ok: false,
        errorCode: 'INVALID_PAYMENT_HEADER',
        status: 400,
        details: parseResult.error || 'Invalid payment header format',
      };
    }
    
    const parsed = parseResult.parsed;
    
    // Build CDP verify request
    const verifyRequest: CDPVerifyRequest = {
      payment: paymentHeader,
      paymentPayload: {
        x402Version: 2,
        scheme: 'exact',
        network: options.paymentRequirements.network,
        payload: {
          signature: parsed.signature,
          authorization: parsed.authorization,
        },
      },
      paymentRequirements: options.paymentRequirements,
    };
    
    // Verify with CDP
    const cdp = getCDPFacilitator();
    const verifyResult = await cdp.verifyPaymentWithRetry(verifyRequest);
    
    if (verifyResult.success && verifyResult.valid) {
      logger.info({
        ...logContext,
        cdpStatus: 'success',
        transactionHash: verifyResult.transactionHash,
        amount: parsed.authorization.value,
      }, 'Payment verified successfully');
      
      return {
        ok: true,
        reference: verifyResult.transactionHash || parsed.txHash || 'verified',
        amount: parsed.authorization.value,
        asset: options.paymentRequirements.asset,
        network: options.paymentRequirements.network,
        transactionHash: verifyResult.transactionHash || parsed.txHash,
      };
    } else {
      const errorCode = verifyResult.error || 'VERIFICATION_FAILED';
      logger.error({
        ...logContext,
        cdpStatus: 'failed',
        errorCode,
        errorDetails: verifyResult.errorDetails,
      }, 'Payment verification failed');
      
      return {
        ok: false,
        errorCode,
        status: 400,
        details: verifyResult.error || 'Payment verification failed',
      };
    }
  } catch (error) {
    logger.error({
      ...logContext,
      error,
      cdpStatus: 'error',
    }, 'Payment verification error');
    
    return {
      ok: false,
      errorCode: 'VERIFICATION_ERROR',
      status: 500,
      details: error instanceof Error ? error.message : 'Unknown verification error',
    };
  }
}

